"use client"

import { useState, useEffect } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { fadeIn, slideInRight, staggerContainer, fadeInUp } from "@/lib/animationVariants"

const navItems = [
  { id: "hero", label: "Home" },
  { id: "about", label: "About" },
  { id: "services", label: "Services" },
  { id: "demo", label: "Demo" },
  { id: "portfolio", label: "Portfolio" },
  { id: "achievements", label: "Achievements" },
  { id: "testimonials", label: "Testimonials" },
  { id: "contact", label: "Contact" },
]

export default function Navigation() {
  const [isScrolled, setIsScrolled] = useState(false)
  const [activeSection, setActiveSection] = useState("hero")
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      try {
        // Update navbar background
        setIsScrolled(window.scrollY > 50)

        // Update active section based on scroll position
        const sections = navItems.map(item => {
          const element = document.getElementById(item.id)
          if (element) {
            return {
              id: item.id,
              offset: Math.abs(element.getBoundingClientRect().top)
            }
          }
          return { id: item.id, offset: Infinity }
        })

        const currentSection = sections.reduce((acc, curr) => 
          curr.offset < acc.offset ? curr : acc
        )

        setActiveSection(currentSection.id)
      } catch (err) {
        console.error("Error during scroll handling:", err)
      }
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const scrollToSection = (id: string) => {
    const element = document.getElementById(id)
    if (element) {
      const prefersReducedMotion = window.matchMedia(
        "(prefers-reduced-motion: reduce)"
      ).matches

      element.scrollIntoView({ 
        behavior: prefersReducedMotion ? "auto" : "smooth"
      })
    }
    setIsMobileMenuOpen(false)
  }

  return (
    <>
      {/* Desktop Navigation */}
      <motion.nav
        variants={fadeIn}
        initial="hidden"
        animate="visible"
        className={`fixed top-0 left-0 right-0 z-50 hidden md:block transition-all duration-500 ${
          isScrolled ? "bg-white/90 backdrop-blur-md shadow-sm" : "bg-transparent"
        }`}
      >
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-center h-20">
            <ul className="flex space-x-8">
              {navItems.map((item) => (
                <li key={item.id}>
                  <motion.button
                    onClick={() => scrollToSection(item.id)}
                    whileHover={{ scale: 1.05 }}
                    transition={{ type: "spring", stiffness: 400, damping: 17 }}
                    className={`font-montserrat text-sm transition-colors duration-300 relative ${
                      isScrolled 
                        ? activeSection === item.id
                          ? "text-[#D4AF37]"
                          : "text-black hover:text-[#D4AF37]"
                        : "text-white hover:text-[#D4AF37]"
                    }`}
                  >
                    {item.label}
                    {activeSection === item.id && (
                      <motion.div
                        layoutId="activeSection"
                        className="absolute -bottom-1 left-0 right-0 h-0.5 bg-[#D4AF37]"
                        transition={{ 
                          type: "spring", 
                          stiffness: 500, 
                          damping: 30 
                        }}
                      />
                    )}
                  </motion.button>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </motion.nav>

      {/* Mobile Navigation Button */}
      <motion.div 
        variants={fadeIn}
        initial="hidden"
        animate="visible"
        className="md:hidden fixed top-4 right-4 z-50"
      >
        <motion.button
          whileTap={{ scale: 0.95 }}
          onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          className={`p-3 rounded-full transition-all duration-300 ${
            isScrolled || isMobileMenuOpen 
              ? "bg-white text-black shadow-lg" 
              : "bg-black/20 text-white"
          }`}
        >
          {isMobileMenuOpen ? "✕" : "☰"}
        </motion.button>
      </motion.div>

      {/* Mobile Navigation Menu */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            variants={slideInRight}
            initial="hidden"
            animate="visible"
            exit={{ 
              opacity: 0, 
              x: "100%",
              transition: { duration: 0.3, ease: [0.43, 0.13, 0.23, 0.96] }
            }}
            className="fixed inset-0 z-40 md:hidden bg-white/95 backdrop-blur-md"
          >
            <div className="flex flex-col items-center justify-center h-full">
              <motion.ul 
                variants={staggerContainer}
                initial="hidden"
                animate="visible"
                className="space-y-6"
              >
                {navItems.map((item) => (
                  <motion.li 
                    key={item.id}
                    variants={fadeInUp}
                  >
                    <motion.button
                      onClick={() => scrollToSection(item.id)}
                      whileHover={{ scale: 1.05 }}
                      transition={{ type: "spring", stiffness: 400, damping: 17 }}
                      className={`font-montserrat text-lg ${
                        activeSection === item.id
                          ? "text-[#D4AF37]"
                          : "text-black hover:text-[#D4AF37]"
                      }`}
                    >
                      {item.label}
                    </motion.button>
                  </motion.li>
                ))}
              </motion.ul>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  )
}
